.select-wrapper {
  position: relative;
  
  &::before {
    content: "";
    width: 10px;
    height: 5px;
    position: absolute;
    bottom: 35px;
    right: 20px;
    background-image: url("../img/icon-arrow-down.svg");
    background-repeat: no-repeat;
    background-position: center;
    cursor: pointer;
    z-index: 1;
  }
}